@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container{
	box-sizing: border-box;
	height:100vh;
	max-width: 100vw;
	margin:0;
  padding:0;
  position: relative;
  background-color:lavender;
  overflow: scroll;
}

.form form{
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.icon{
  font-size: 35px;
  margin-bottom: 10px;
  text-align: center;
  align-self: center;
  padding: 5px;
}

.form{  
  border: 2px solid lightgrey;
  height: 300px;
}

.form input[type="text"]{
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
  border: none;
  border-bottom: 1px solid grey;
  outline: none;
  background: transparent;
}

.imageContainer{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-column-gap: 6px;
  grid-row-gap:20px;
  margin: 5px;
}

.noUser{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 100vh;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.5); 
  font-family: sans-serif;
  line-height: 1.5;
  font-size: 28px;
}

.guestSignIn:hover{
  cursor: pointer;
}
.uploadModal{
	border: 2px solid lightgrey;
  height: 200px;
}

.uploadModal form{
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;}

.uploadModal input[type="text"]{
  margin:10px 0;
  padding: 10px;
  width: 100%;
}

.userContainer{
	position: relative;
	display: flex;
	justify-content: center;
	padding: 2px;
}

.userContainer > p{
	font-size: 25px;
}

.upload{
	margin-left: 2px;
	color: black;
	font-size: 20px;
	cursor: pointer;
}

.guest{
	visibility: hidden;
}
.image{
	width: 100%;
	display: flex;
	flex-direction: column;
	border:2px solid lightgray;
	background-color: lavender;
	flex-wrap: wrap;
	border-radius: 10px;
}

.imageBody img{
  width:100%;
  height: 300px;
}

a, a:hover{
	color: black;
	text-decoration: none;
}

.imageHeader{
    display: flex;
    padding: 5px;
    align-items: center;
	  border-bottom: 1px solid lightgray;
}

.imageHeader > div{
	margin: 2px;
}

.imageBody:hover{
	cursor: pointer;
	opacity: .5;
}


.imageFooter{
	padding-left: 5px;
	border-top: 1px solid lightgray;
	word-break: break-all;
}

.count{
	color:red;
}

.count:active{
	-webkit-animation: beat ease-in-out .2s;
	        animation: beat ease-in-out .2s;
}

.count:hover{
	cursor: pointer;
}

.lightbox{
	display: flex;
	color: black;
	height: 400px;
}

.lightboxImage{
	width: 50%;
	position: relative;
}

.lightboxImage img{
	display: block;
	height: 100%;	
	width: 100%;
}

.lightboxComments{
	border-left: 1px solid lightgray;
	background-color: whitesmoke;
	color: black;
	flex: 2 1;
	position: relative;
	display: flex;
	flex-direction: column;
	 word-break: break-all;
}


.text{
	height: 50px;
	background-color: black;
	border: 1px solid black;
}

.lightboxComments input[type="text"]{
  border: none;
  outline: none;
  background: transparent;
  width: 80%;
}

form button{
	float: right;
	border: none;
	outline: none;
	background-color: transparent;
	color: blue;
}

form{
	border: 1px solid lightgrey;
	width: 100%;
	height: 100%;
}

@-webkit-keyframes beat{
  0%{
    -webkit-transform:scale(1);
            transform:scale(1);
  } 50%{
    -webkit-transform:scale(1.5);
            transform:scale(1.5);
  } 100%{
    -webkit-transform:scale(1);
            transform:scale(1);
  }
}

@keyframes beat{
  0%{
    -webkit-transform:scale(1);
            transform:scale(1);
  } 50%{
    -webkit-transform:scale(1.5);
            transform:scale(1.5);
  } 100%{
    -webkit-transform:scale(1);
            transform:scale(1);
  }
}
.profileHeader{
	display: flex;
	justify-content: center;
	align-items: center;
	height: 250px;
	border-bottom: 1px solid lightgrey;
}

.info{
	display: flex;
	font-size: 18px;
}

.info > div{
	padding-right: 10px;	
}

.profile{
	border:1px solid black ;
}

.summary{
	margin-left: 15px;
	padding: 5px;
	font-size: 16px;
}


.profileContainer{
	background-color: lavender;
	padding: 5px;
	height: 100vh;
	box-sizing: border-box;
	max-width: 100vw;
	overflow: scroll;
}

.profileBody{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-column-gap: 6px;
  grid-row-gap:20px;
  margin: 5px;
}

.panel{
	display: flex;
	justify-content:space-around;
	padding: 10px;
}

.panel1, .panel2, .panel3{
	cursor: pointer;
	color: grey;
}

.active{
	color: black;
	font-weight: bold;
}

.userProfile{
	display: flex;
}

.follow{
	margin: 3px;
	cursor: pointer;
	background: transparent;
	border: 1px solid black;
	outline: none;
	color: black;
	padding:0  8px;
}

.nav{
  position: -webkit-sticky;
  position: sticky;
  top:0;
  background-color: lightblue;
  display:flex;
  height:8vh;
  align-items:center;
  justify-content: space-between;
  z-index: 1000;
  box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.75);
  flex-wrap: wrap;
  padding: 5px;
}

.title{
  font-family:Satisfy;
  font-size:2rem;
  color:white;
}

.title a,.title a:hover{
  text-decoration: none;
  color: white;
}

.buttonContainer{
  max-width: 100%
}

.signOut{
  border-radius: 30px;
  float: right;
  margin-left: 3px;
}

.signUp{
  float: right;
  border-radius: 30px;
}

.login{
  float: right;
  margin-right: 2px;
  border-radius: 30px;
}

.nav > div{
  margin: 5px;
}

@media only screen and (max-width: 330px) {
  .title{
    font-size: 1.5rem;
  }

  .buttonContainer{
    flex: 2 1;
  }
}

@media only screen and (max-width: 660px)
{
    input[type="search"]{
    display: none;
  }
}

